'use strict';

var React = require('react');

module.exports = React.createClass({
  displayName: 'Content',

  getInitialState: function getInitialState() {
    return { serverData: null };
  },

  refreshData: function refreshData() {
    var _this = this;

    // replace this with your favourite library for doing ajax calls
    var xhr = new XMLHttpRequest();
    xhr.open('get', '/api/currentTime', true);
    xhr.onload = function () {
      var data = JSON.parse(xhr.responseText);
      _this.setState({ serverData: data.time });
    };
    xhr.send();
  },

  render: function render() {
    return React.createElement(
      'div',
      null,
      React.createElement(
        'p',
        null,
        'Here is some Content ',
        React.createElement(
          'b',
          { ref: 'serverResponse' },
          this.state.serverData || 'Click the button to hit the API'
        )
      ),
      React.createElement('input', { ref: 'refreshButton', type: 'button', onClick: this.refreshData, value: 'Hit the server' })
    );
  }

});


